import React from 'react';
import Table from '../common/table/Table';


const UsersTable = (props: any) => {

    const columns ={
        id: {path: 'id', name: 'ID'},
        name: {path: 'name', name: ''},
        role: {path: 'role', name: ''},
        ctime: {path: 'ctime', name: ' '},
        actions: {path: 'actions', name: '', component: (user: any) => (
                <button
                    onClick={() => props.onDelete(user.id)}
                    className="btn btn-danger"
                >
                    
                </button>
            )}
    }

    return (
        <Table
            columns={columns}
            data={props.users}
        />
    )
}

export default UsersTable;