import React from 'react';
import Table from '../common/table/Table';


const EventsTable = (props: any) => {

    const columns = {
        ctime: {path: 'ctime', name: ' '},
        event: {path: 'event', name: ''},
    }

    return (
        <Table
            columns={columns}
            data={props.comingEvents}
        />
    )
}

export default EventsTable;